import { Component, OnInit } from '@angular/core';
import { FormsModule, FormGroup, FormControl } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService } from "../../shared/services";
import { Http } from '@angular/http';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent implements OnInit {
	defaultNull:string = "-";
	employees:any[];
	formSubmitted:boolean;
	// [{"_id":"w1","_rev":"4-2ab2563cd08a90e95c6f9a7532c25b52","type":"worker","w_id":"0001","w_name":"Adam Smith","w_age":"30","w_photo":"null","w_email":"ccc@gmail.com","w_bloodtype":"A","w_height":"172","w_weight":"73","goggles_on":"true","goggles_id":"11111","helmet_on":"true","helmet_id":"11111","vest_on":"false","vest_id":"11111","boots_on":"true","boots_id":"11111","w_p_id":"PJ002","w_on_duty":"true"},{"_id":"w2","_rev":"5-80135927f60ef52f67d61287f49e6924","type":"worker","w_id":"0002","w_name":"Lee Yi","w_age":"44","w_photo":"null","w_email":"ccc@gmail.com","w_bloodtype":"O","w_height":"175","w_weight":"80","goggles_on":"false","goggles_id":"22222","helmet_on":"false","helmet_id":"22222","vest_on":"false","vest_id":"22222","boots_on":"true","boots_id":"22222","w_p_id":"PJ002","w_on_duty":"true"},{"_id":"w5","_rev":"6-84c05e7b127cfcfe748bb03b784d3ea9","type":"worker","w_id":"0005","w_name":"John Steve","w_age":"28","w_photo":"null","w_email":"hhh@gmail.com","w_bloodtype":"B","w_height":"175","w_weight":"75","goggles_on":"true","goggles_id":"55555","helmet_on":"true","helmet_id":"55555","vest_on":"true","vest_id":"55555","boots_on":"true","boots_id":"55555","w_p_id":"PJ001","w_on_duty":"true"},{"_id":"w6","_rev":"2-8996a402daa40ba5ef1655f21157ad33","type":"worker","w_id":"0006","w_name":"Karl Marx","w_age":"38","w_photo":"null","w_email":"ggg@gmail.com","w_bloodtype":"AB","w_height":"178","w_weight":"85","goggles_on":"true","goggles_id":"66666","helmet_on":"false","helmet_id":"66666","vest_on":"false","vest_id":"66666","boots_on":"true","boots_id":"66666","w_p_id":"null","w_on_duty":"false"},{"_id":"w4","_rev":"6-11349936c9c540585df2b151a7ebe8da","type":"worker","w_id":"0004","w_name":"Hegel","w_age":"22","w_photo":"null","w_email":"123@gmail.com","w_bloodtype":"B","w_height":"170","w_weight":"66","goggles_on":"true","goggles_id":"44444","helmet_on":"false","helmet_id":"44444","vest_on":"false","vest_id":"44444","boots_on":"true","boots_id":"44444","w_p_id":"PJ003","w_on_duty":"true"},{"_id":"w3","_rev":"6-c27ca179298f0f5cb07ec6aba32a52e7","type":"worker","w_id":"0003","w_name":"Abu Abim","w_age":"24","w_photo":"null","w_email":"abc@gmail.com","w_bloodtype":"O","w_height":"172","w_weight":"80","goggles_on":"true","goggles_id":"33333","helmet_on":"true","helmet_id":"33333","vest_on":"true","vest_id":"33333","boots_on":"true","boots_id":"33333","w_p_id":"PJ001","w_on_duty":"true"}];
	//whether the model of app-add-employee is being display or not
	displayModel:boolean=false;
	filterQuery:string="";
	rowsOnPage:number = 10;
	sortBy:string = "w_id";
	sortOrder:string = "asc";
	w_id:string = "";
	constructor(private CallApi:CallApiService,
              	private reloadService:ApiReloadService,
              	private http:Http
				) {
		this.reloadService.reload$.subscribe(
		  (reload)=>{
		    if(reload){
		    	// console.log("reload employees");
		    	this.employees=null;
		    	//reload list when new employee is added
				setTimeout(() => this.getList(ApiCmd.workerList), 2000);
		    }
		  }
		);

	}

	ngOnInit() {
		this.getList(ApiCmd.workerList);
	}

	// get worker list
	getList(url:string){
		this.CallApi.getData(url)
			.subscribe(data=>{
				for(let worker in data){
					console.log(data[worker]);
					if((data[worker]['w_location']==='-' || data[worker]['w_location']==='') && data[worker].w_dev_data!=null && data[worker].w_dev_data.latitude != null && data[worker].w_dev_data.longitude != null){
						this.CallApi.getLocationByLatLng(this.http,{lat:data[worker].w_dev_data.latitude, lng:data[worker].w_dev_data.longitude},null).subscribe(ditto=>{
							data[worker]['w_location'] = ditto;
						});
					}
				};
				this.employees=data;
				if(this.employees.length > 0){
					this.w_id = this.employees[this.employees.length-1]['w_id'];
					this.w_id = "W"+(parseInt(this.w_id.substr(1))+1);
				}else{
					this.w_id = "W1";
				}
	  		})
	}

	openModalBox():void{
		this.displayModel=true;
	}

	closeModalBox():void{
		this.displayModel=false;
	}

	//accept eventEmitter to let use know form submitted
	formSubmit(event){
		this.displayModel=false;
		this.formSubmitted=event;
	}

	//function to close the notification alert
	closeAlert(){
		this.formSubmitted=false;
	}
}



