import { Component, OnInit, OnChanges, Input } from '@angular/core';

@Component({
	selector: 'app-sensor-data',
	templateUrl: './sensor-data.component.html',
	styleUrls: ['./sensor-data.component.scss']
})
export class SensorDataComponent implements OnInit, OnChanges {
	@Input() sensorData:any[] = [{'hr':0, 'steps':0, 'steps_total':0, 'battery':0}];
	sensorCard: NumberCard;

	green = '#5AA454';
	red = '#A10A28';
	yellow = '#C7B42C';

	constructor() { }

	ngOnChanges(){
		this.updateCard();
	}

	ngOnInit() {
		this.updateCard();
	}

	updateCard(){
		console.log(this.sensorData);
		if(this.sensorData !== null && typeof this.sensorData !== 'undefined'){
			let hr = this.sensorData['hr'];
			let steps = this.sensorData['steps'];
			let steps_total = this.sensorData['steps_total'];
			let battery = this.sensorData['battery'];
			let data = [
				{'name':'Heart Rate', 'value': hr+' bpm'},
				{'name':'Number of Steps', 'value': steps},
				{'name':'Total Number of Steps', 'value': steps_total},
				{'name':'Battery Level', 'value': battery+'%'}
			];
			this.sensorCard = new NumberCard(data);

			let color_hr = this.red;
			let color_steps = this.red;
			let color_steps_total = this.red;
			let color_battery = this.red;
			if(hr>=60 && hr<=100){color_hr = this.green;}
			if(steps!=0){color_steps = this.green;}
			if(steps_total!=0){color_steps_total = this.green;}
			if(battery>=50){color_battery = this.green;} else if(battery>=20){color_battery = this.yellow}

			this.sensorCard.colorScheme = {domain:[color_hr,color_steps,color_steps_total,color_battery]};
		}
	}
}

export class NumberCard{
	data: any [];
	view: any[] = [1000, 200];

	colorScheme = {
		domain: ['#5AA454', '#A10A28', '#C7B42C']
	};

	cardColor = "#f5f5f5";

	constructor(data:any[]){
		this.data = data;
	}

	onSelect(event){
		// console.log(event);
	}
}


