import { Component,Input, Output, OnInit,EventEmitter } from '@angular/core';
import {Router, ActivatedRoute, Params,NavigationEnd,ActivatedRouteSnapshot} from '@angular/router';
import { AuthService } from "../../shared";
import { TabService } from "../../shared/services";
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss'],
	animations:[
	//animation of content when sidebar move
	trigger('move',[
		state('sideBarWithDesc',style({
			'margin-left': '200px'
		})),
		state('sideBarWithIcon',style({
			'margin-left': '80px'
		})),
		transition('sideBarWithDesc=>sideBarWithIcon',animate('400ms ease-in-out')),
		transition('sideBarWithIcon=>sideBarWithDesc',animate('400ms ease-in-out'))
		])
	]
})
export class HeaderComponent implements OnInit {
	title: string;
	profile: object;
	headeroffsetShow:string = "200px";
	headeroffsetHide:string = "80px";
	headerTitleShow: string = "sideBarWithDesc";
	headerTitle: any;
	@Output()
	onShow = new EventEmitter<string>();
	show: string = "showAll";
	constructor(private router: Router,
		private activatedRoute: ActivatedRoute,
		private authService: AuthService,
		private tabService:TabService) {
		//get the signin in user data
		this.profile = authService.getUserData();
		router.events.subscribe(event => {
			if (event instanceof NavigationEnd) {
				this.title = this.getTitle(router.routerState, router.routerState.root).join('-');
				// console.log('title', this.title);
				//set the default tab for each page
				// console.log("current tab "+this.tabService.currentProject);
				if(this.title=="projects"&&!(this.tabService.currentProject=="All"||this.tabService.currentProject=="Ongoing"||this.tabService.currentProject=="Attached"||this.tabService.currentProject=="Past")){
					// console.log("aiya project");
					this.tabService.setTab("Ongoing");
				}
				else if(this.title=="report"&&!(this.tabService.currentProject=="All"||this.tabService.currentProject=="Current"||this.tabService.currentProject=="Graph")){
					// console.log("aiya report");
					this.tabService.setTab("All");
				}
				else if(this.title=="settings"&&!(this.tabService.currentProject=="User"||this.tabService.currentProject=="Profile")){
					// console.log("aiya settings");
					this.tabService.setTab("Profile");
				}
			}
		});
		// console.log(this.profile);
	}

	//get the title from data of the route
	getTitle(state, parent) {
		var data = [];
		if (parent && parent.snapshot.data && parent.snapshot.data.page) {
			data.push(parent.snapshot.data.page);
		}

		if (state && parent) {
			data.push(... this.getTitle(state, state.firstChild(parent)));
		}
		return data;
	}

	ngOnInit() {
		this.headerTitle = document.getElementById("headerTitle");
	}

	toggleCollapse() {
		this.show = this.show === "showAll" ? "showIcon" : "showAll";
		this.onShow.emit(this.show);
    	this.headerTitleShow=this.headerTitleShow==='sideBarWithDesc'?'sideBarWithIcon':'sideBarWithDesc';
	}



}



