import { Component, OnInit, Input, OnChanges } from '@angular/core';

@Component({
  selector: 'app-on-duty-chart',
  templateUrl: './on-duty-chart.component.html',
  styleUrls: ['./on-duty-chart.component.scss']
})
export class OnDutyChartComponent implements OnInit, OnChanges {
@Input() dutyChart: any[] = [];

  chartData: BarChart;

  constructor() { 
  }

  ngOnChanges(){
    this.updateChart();
  }

  ngOnInit() {
    this.updateChart();
  }

  updateChart(){
    console.log(this.dutyChart);
    if(this.dutyChart !== null && (typeof this.dutyChart !== 'undefined')){
      let chartArray = [];
      for(let ptr in this.dutyChart){
        let data = this.dutyChart[ptr];
        // console.log(data);
        chartArray.push({
          'name': data.pname,
          'series': [
          { "name": "ok", "value": parseInt(data.ok) },
          { "name": "unhealthy", "value": parseInt(data.unhealthy) },
          { "name": "not on duty", "value": parseInt(data.workernum)-parseInt(data.onduty) }
          ]
        });
      }
      // console.log(chartArray);
      this.chartData = new BarChart(chartArray);
    }
  }

  onSelect(event){
    console.log(event);
    for(let project of this.dutyChart){
      if(project['pname'] === event['series']){
        console.log(project);
        document.location.href = '#/projects/'+project['pid'];
      }
    }
  }

}

export class BarChart{

  constructor(data:any[]){
    this.multi = data;
  }

  multi: any[];

  view: any[] = [, 399];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = true;
  showXAxisLabel = true;
  xAxisLabel = 'Worker';
  showYAxisLabel = true;
  yAxisLabel = 'Project Name';

  colorScheme = {
    domain: ['#90e87b', '#FF6756', '#b0b0b0']
  };
  
}



